html {
  box-sizing: border-box;
  touch-action: manipulation;
  -webkit-tap-highlight-color: transparent;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Nunito", "Arial", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  margin: 0;
}

code {
  font-family: Consolas, "Courier New", monospace;
}

.Icon{fill:none;transition:-webkit-transform 0.3s;transition:transform 0.3s;transition:transform 0.3s, -webkit-transform 0.3s}.Icon--infographic{background-color:#aaa;padding:10px;stroke:#fff;stroke-width:6;width:50px}.Icon--profit{background-color:#29bfd7}.Icon--users{background-color:#bcd74a}.Icon--orders{background-color:#fe9833}.Icon--complaints{background-color:#fa5050}.Icon--dropdown{fill:#555;width:15px}.Icon--minimize,.Icon--maximize{fill:inherit;width:20px;padding:0;height:100%}.Icon--export,.Icon--print{padding:7px;width:40px}.Icon--indicator{margin-right:5px;stroke:inherit;stroke-width:10;width:20px}.Icon--rotated{-webkit-transform:rotate(180deg);transform:rotate(180deg)}

.TextPanel{align-items:center;background-color:#fff;border-radius:5px;box-shadow:0 2px 5px rgba(0,0,0,0.3);display:grid;grid-template-areas:"percent percent" "value   value  " "header  header ";grid-template-columns:1fr auto;grid-template-rows:1fr auto;overflow:hidden;width:100%}.TextPanel--profit{grid-area:profit}.TextPanel--users{grid-area:users}.TextPanel--orders{grid-area:orders}.TextPanel--complaints{grid-area:complaints}.TextPanel__header{align-items:center;background-color:#ddd;display:flex;grid-area:header;justify-content:space-between;width:100%}.TextPanel__heading{font-size:1em;text-transform:uppercase}.TextPanel__heading,.TextPanel__value,.TextPanel__percentage{margin:0;padding:10px}.TextPanel__value{font-size:1.5em;grid-area:value;padding-top:0}.TextPanel__percentage{align-items:center;display:flex;justify-content:center;font-size:1.2em;grid-area:percent;justify-self:end;padding-bottom:0}.TextPanel__percentage--positive{color:#bcd74a;stroke:#bcd74a}.TextPanel__percentage--negative{color:#fa5050;stroke:#fa5050}

.VisualPanel{background-color:#fff;border-radius:5px;box-shadow:0 2px 5px rgba(0,0,0,0.3);position:relative;overflow:hidden}.VisualPanel--histogram{grid-area:histogram}.VisualPanel--map{grid-area:map;position:relative}.VisualPanel--summary{grid-area:summary}.VisualPanel__header{align-items:center;background-color:#fff;display:flex;justify-content:space-between;left:0;position:absolute;top:0;width:100%;z-index:1}.VisualPanel__heading{font-size:1.2em;margin:0;padding:10px;text-transform:uppercase}.VisualPanel__button{background:none;border:none;color:#555;cursor:pointer;font-family:'Nunito';font-size:1em;font-weight:bold;outline:none;padding:0;text-transform:uppercase;align-items:center;display:flex;justify-content:center;fill:#555;padding:5px}.VisualPanel__button:hover{background-color:#aaa;fill:#fff}

.Dropdown{position:relative}.Dropdown__button{background:none;border:none;color:#555;cursor:pointer;font-family:'Nunito';font-size:1em;font-weight:bold;outline:none;padding:0;text-transform:uppercase;align-items:center;display:flex;justify-content:space-between;min-width:180px;padding:10px}.Dropdown__button--active{background-color:#ccc}.Dropdown__button:hover{background-color:#ccc}.Dropdown__list{background-color:#eee;box-shadow:0 2px 5px rgba(0,0,0,0.3);left:0;list-style-type:none;margin:0;padding:0;position:absolute;top:100%;width:100%;z-index:2}

.Button{background:none;border:none;color:#555;cursor:pointer;font-family:'Nunito';font-size:1em;font-weight:bold;outline:none;padding:0;text-transform:uppercase;align-items:center;display:flex;justify-content:center;background-color:#ccc;justify-content:space-between;min-width:150px;text-transform:uppercase}.Button__text{padding:10px}

.Histogram{background-color:#eee;width:100%;height:100%}.tooltip__heading,.tooltip__text{font-size:1rem;margin:0;text-transform:capitalize}.tooltip__heading{margin-bottom:5px}

.Map{align-items:center;display:flex;justify-content:center;width:100%;height:100%;background-color:#eee}.hoverinfo{border:none !important;border-radius:5px !important;box-shadow:0 2px 5px rgba(0,0,0,0.3) !important;font-family:'Nunito', sans-serif;font-size:1rem !important;padding:10px !important}

.App{align-items:center;display:flex;justify-content:center;flex-direction:column;background-color:#eee;display:flex;color:#555;font-weight:normal;height:100%;min-height:100vh;padding:30px;width:100%}.App__header,.App__footer{align-items:center;display:grid;grid-column-gap:20px;-webkit-column-gap:20px;column-gap:20px;width:100%}.App__header{grid-area:header}.App__header--stats{grid-template-columns:auto 1fr auto}.App__header--analytics{grid-template-columns:auto 1fr repeat(3, auto)}.App__heading{color:#555;font-size:1.5em;margin:0}.App__section{background-color:#ddd;border-radius:5px;box-shadow:inset 0 2px 20px rgba(0,0,0,0.1),inset 0 2px 5px rgba(0,0,0,0.3);display:grid;grid-gap:20px;gap:20px;max-width:1200px;padding:30px;width:100%}.App__section--stats{grid-template-columns:repeat(4, 1fr);grid-template-areas:"header header header header    " "profit users  orders complaints";margin:30px 0}.App__section--analytics{grid-template-columns:repeat(2, 1fr);grid-template-areas:"header    header" "histogram map   " "summary   map   " "footer    footer";grid-template-rows:auto 300px 250px auto}.App__section--histogramMax{grid-template-areas:"header    header   " "histogram histogram" "summary   map      " "footer    footer   ";grid-template-rows:auto 300px 300px auto}.App__section--mapMax{grid-template-areas:"header    header " "map       map    " "histogram summary" "footer    footer ";grid-template-rows:auto 500px 250px auto}.App__section--summaryMax{grid-template-areas:"header    header " "histogram map    " "summary   summary" "footer    footer "}.App__footer{grid-area:footer;grid-template-columns:1fr repeat(2, auto)}.App__info{margin:0}

